import * as actionType from "../actions/actionTypes";
import { updateObject } from "../utility";

const INITIAL_STATE = {
  orders: {},
};

const onFetchData = (state, action) => {
  const newObj = { ...state.orders };
  Object.keys(action.data).map((objEl) => {
    newObj[objEl] = action.data[objEl];
  });
  return updateObject(state, { orders: newObj });
};

const purchaseBurgerSuccess = (state, action) => {
  const newObj = { ...state.orders };
  newObj[action.orderId] = action.orderData;
  return updateObject(state, { orders: newObj });
};

const purchaseBurgerFailure = (state, action) => {
  console.log(action.error);
  return state;
};

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case actionType.PURCHASE_BURGER_SUCCESS:
      return purchaseBurgerSuccess(state, action);
    case actionType.PURCHASE_BURGER_FAILURE:
      return purchaseBurgerFailure(state, action);
    case actionType.ON_FETCH_DATA:
      return onFetchData(state, action);
    default:
      return state;
  }
};

export default reducer;
