import React, { Component } from "react";
import Button from "../../components/UI/Button/Button";
import Input from "../../components/UI/Input/Input";
import classes from "./Auth.module.css";

class Auth extends Component {
  state = {
    controls: {
      username: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "",
          label: "Username:",
        },
        value: "",
        validation: {
          required: true,
          minLength: 5,
        },
        isValid: false,
        isTouched: false,
      },
      password: {
        elementType: "password",
        elementConfig: {
          type: "password",
          placeholder: "",
          label: "Password:",
        },
        value: "",
        validation: {
          required: true,
          minLength: 5,
        },
        isValid: false,
        isTouched: false,
      },
    },
  };
  validityCheck(element, rules) {
    let isValid = true;
    if (rules?.required) isValid = element.trim() !== "" && isValid;
    if (rules?.minLength)
      isValid = element.length >= rules.minLength && isValid;
    if (rules?.maxLength)
      isValid = element.length <= rules.maxLength && isValid;
    return isValid;
  }

  formValidityCheck() {
    return Object.keys(this.state.controls).reduce(
      (prev, cur) => prev && this.state.controls[cur].isValid,
      true
    );
  }

  inputChangedHanler = (event, inputIdentifier) => {
    const updatedOrderForm = {
      ...this.state.controls,
    };
    const updatedFormElement = { ...updatedOrderForm[inputIdentifier] };
    updatedFormElement.value = event.target.value;
    updatedFormElement.isValid = this.validityCheck(
      updatedFormElement.value,
      updatedOrderForm[inputIdentifier].validation
    );
    updatedFormElement.isTouched = true;
    updatedOrderForm[inputIdentifier] = updatedFormElement;
    this.setState({ controls: updatedOrderForm });
  };

  render() {
    const inputs = Object.keys(this.state.controls).map((key) => {
      return (
        <Input
          key={key}
          elementType={this.state.controls[key].elementType}
          name={key}
          elementConfig={this.state.controls[key].elementConfig}
          defaultValue={this.state.controls[key].value}
          changed={(event) => this.inputChangedHanler(event, key)}
          submited={this.formSubmitHandler}
          validation={this.state.controls[key].validation}
          invalid={!this.state.controls[key].isValid}
          isTouched={!this.state.controls[key].isTouched}
        />
      );
    });
    return (
      <div className={classes.Auth}>
        <h4>Login</h4>
        <form onSubmit={this.orderHandler}>
          {inputs}
          <Button
            btnType="Success"
            type="submit"
            disabled={!this.formValidityCheck()}
          >
            LOGIN
          </Button>
        </form>
      </div>
    );
  }
}

export default Auth;
