import * as actionTypes from "./actionTypes";
import axios from "../../axios-orders";

const purchaseBurgerSuccess = (id, orderData) => {
  console.log(orderData);
  return {
    type: actionTypes.PURCHASE_BURGER_SUCCESS,
    orderId: id,
    orderData: orderData,
  };
};

const purchaseBurgerFailure = (error) => {
  return {
    type: actionTypes.PURCHASE_BURGER_FAILURE,
    error: error,
  };
};

export const purchaseBurgerStart = (orderData, history) => {
  return (dispatch) => {
    axios
      .post("/orders.json", orderData)
      .then((response) => {
        history.push("/");
        dispatch(
          purchaseBurgerSuccess(
            response.data.name,
            JSON.parse(response.data.config)
          )
        );
      })
      .catch((error) => {
        dispatch(purchaseBurgerFailure(error));
      });
  };
};

const onFetchData = (data) => {
  return {
    type: actionTypes.ON_FETCH_DATA,
    data: data,
  };
};

export const fetchData = () => {
  return (dispatch) => {
    axios
      .get("/orders.json")
      .then((response) => {
        dispatch(onFetchData(response.data));
      })
      .catch((error) => console.log(error));
  };
};
