import React, { Component } from "react";
import Order from "./Order/Order";
import classes from "./Orders.module.css";
import axios from "../../axios-orders";
import { connect } from "react-redux";
import * as actionCreators from "../../store/actions/index";

class Orders extends Component {
  componentDidMount() {
    this.props.fetchData();
  }

  render() {
    let orders = {};
    console.log(this.props);
    if (this.props.orders) {
      orders = Object.keys(this.props.orders).map((key) => (
        <Order
          ingredients={this.props.orders[key].ingredients}
          price={this.props.orders[key].price}
          customerName={this.props.orders[key].customer.name}
          key={key}
        />
      ));
    } else orders = null;
    return <div className={classes.Orders}>{orders}</div>;
  }
}
const mapStateToProps = (state) => {
  return {
    orders: state.oReducer.orders,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    fetchData: () => dispatch(actionCreators.fetchData()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Orders);
